<template>
	<div class="expand-box" :style="{height: this.$root.height+'px'}">
		<div class="expand-header">
			<a href="/bigdata">
				<el-button type="primary" icon="el-icon-arrow-left" class="button">返回
				</el-button>
			</a>
			<h1 class="title">淘宝双11数据分析</h1>
			<div class="current-time">{{currentTime}}</div>
		</div>

		<div class="expand-body" :style="{height: this.$root.height-70+'px'}">
			<div class="column">
				<BarPanel style="height: 32%;"></BarPanel>
				<LinePanel style="height: 32%;"></LinePanel>
				<PiePanel style="height: 32%;"></PiePanel>
			</div>
			<div class="main-column">
				<MainPanel style="height: 100%;"></MainPanel>
			</div>
			<div class="column">
				<BarPanel2 style="height: 32%;"></BarPanel2>
				<LinePanel2 style="height: 32%;"></LinePanel2>
				<PiePanel2 style="height: 32%;"></PiePanel2>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Expand',
		components: {
			BarPanel: () => import('../components/expand/BarPanel'),
			BarPanel2: () => import('../components/expand/BarPanel2'),
			LinePanel: () => import('../components/expand/LinePanel'),
			LinePanel2: () => import('../components/expand/LinePanel2'),
			PiePanel: () => import('../components/expand/PiePanel'),
			PiePanel2: () => import('../components/expand/PiePanel2'),
			MainPanel: () => import('../components/expand/MainPanel')
		},
		data() {
			return {
				currentTime: null,
				timer: null
			}
		},
		methods: {
			setTime() {
				let time = new Date();
				this.currentTime = time.toLocaleString();
			},
			setTimer() {
				let timer = setInterval(this.setTime, 1000);
				this.$once("hook:beforeDestroy", () => {
					clearInterval(this.timer);
				});

			}
		},
		mounted() {
			if (this.$root.width < 1024 || this.$root.height < 550) {
				setTimeout(() => {
					alert("页面可能不适配当前分辨率 (" + this.$root.width + "*" + this.$root.height + ")")
				}, 1000);
			}
			this.setTimer();
		}
	}
</script>

<style>
	.expand-box * {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
	}

	@font-face {
		font-family: electronicFont;
		src: url(../assets/expand/DS-DIGIT.TTF);
	}

	.expand-box {
		background: url(../assets/expand/bg.jpg) no-repeat top center;
	}

	.expand-header {
		height: 60px;
		background: url(../assets/expand/head_bg.png) no-repeat top center;
		background-size: 100% 100%;
	}

	.expand-header .button {
		position: absolute;
		left: 5px;
		top: 3px;
		font-size: 10px;
		padding-right: 3px;
		line-height: 20px;
		color: #ccccff;
		border-width: 1px !important;
		color: #fff !important;
		background-color: transparent !important;
	}

	.expand-header .title {
		font-size: 40px;
		color: #fff;
		text-align: center;
		line-height: 60px;
		text-shadow: 2px 2px 10px darkkhaki;
	}

	.expand-header .current-time {
		position: absolute;
		right: 5px;
		top: 5px;
		font-size: 10px;
		line-height: 20px;
		color: #ccccff;
	}

	.expand-body {
		display: flex;
		min-width: 1024px;
		max-width: 1920px;
		margin: 0 auto;
		padding: 10px;
	}

	.expand-body .column {
		flex: 3;
		height: 100%;
	}

	.expand-body .main-column {
		flex: 5;
		margin: 10px 10px;
		height: 100%;
		overflow: hidden;
	}

	.expand-body .panel {
		position: relative;
		height: 30;
		padding: 10px 10px;
		border: 1px solid rgba(25, 186, 139, 0.17);
		margin-bottom: 10px;
		background: url(../assets/expand/line.png) rgba(255, 255, 255, 0.03);
	}

	.expand-body .panel::before {
		position: absolute;
		top: 0;
		left: 0;
		width: 10px;
		height: 10px;
		content: "";
		border-top: 2px solid #02a6b5;
		border-left: 2px solid #02a6b5;
	}

	.expand-body .panel::after {
		position: absolute;
		top: 0;
		right: 0;
		width: 10px;
		height: 10px;
		content: "";
		border-top: 2px solid #02a6b5;
		border-right: 2px solid #02a6b5;
	}

	.expand-body .panel .panel-footer {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
	}

	.expand-body .panel .panel-footer::before {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 10px;
		height: 10px;
		content: "";
		border-bottom: 2px solid #02a6b5;
		border-left: 2px solid #02a6b5;
	}

	.expand-body .panel .panel-footer::after {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 10px;
		height: 10px;
		content: "";
		border-bottom: 2px solid #02a6b5;
		border-right: 2px solid #02a6b5;
	}

	.expand-body .panel h2 {
		height: 24px;
		color: #fff;
		text-align: center;
		line-height: 24px;
		font-size: 14px;
		font-weight: 400;
	}

	.expand-body .panel .chart {
		height: 90%;
	}
</style>
